import React from "react";
import s from './FileAppCss.module.css';

const FileAppCss = () => {
    const appcss =
`.App {
    text-align: center;
} ` 


    return (
        <div className={s.container}>
            <p>В файл App.css:</p>
            <div className="code">
                <code>
                    {appcss}
                </code>
            </div>
            <p></p>
        </div>
    )
}

export default FileAppCss;