let strStoreIndex = 
`import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import props from './redus/state';

const root = ReactDOM.createRoot(document.getElementById('root'));

root.render(
  <React.StrictMode>
    <App state={props.state}/>
  </React.StrictMode>
);
`
export default strStoreIndex;
