import React from "react";
import s from "./FileAppJs.module.css"

const FileAppJs = () => {

    const text =
`import './App.css';
import React from 'react';

function App() {
    return (
    <div className="App">
        Это начало!
    </div>
    );
}
    
export default App;` 


    return (
        <div className={s.container}>
            <p>В файл App.js:</p>
            <div className="code">
                <code>
                    {text}
                </code>
            </div>
        </div>
    )
}

export default FileAppJs;