import React, { Fragment, useState } from 'react';
import s from './StoreIndex.module.css';
import str from './strStoreIndex';



let unhide = (data) => {

    return (
        <>
            <p>Скопировать содержание index.js &nbsp;&nbsp;<span className={s.btnColor} onClick={data.action}> {data.text} </span> </p>
            <div className={s.container}>
                <div  className={`${data.style} ${'code'}`}>
                    <code>
                        {str}
                    </code>
                </div>
                {data.action.name === 'dropUp' &&
                    <p className={s.btnColor} onClick={data.action}>{data.text}</p>
                }
            </div>
        </>
    )
}

const StoreIndex = () => {
    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }
    return (
        <>
            {source && 
                <p>Скопировать содержание index.js &nbsp;&nbsp;<span className={s.btnColor} onClick={drop}>&gt;&gt;</span> </p>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62)})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть'})}
                </Fragment>
            }
        </>
    )

}

export default StoreIndex;