import React, { Fragment, useState } from 'react';
import s from './StoreState.module.css';
import strState from './strStoreState';
import strState61 from './strStoreState61';
import strState62 from './strStoreState62';



let unhide = (data) => {
    let valueState = strState;
    if (data.item === 61) {
        console.log(data.item );
        valueState = strState61;
    }
    if (data.item === 62) {
        console.log(data.item );
        valueState = strState62;
    }

    return (
        <>
            <p>Посмотреть содержание store.js &nbsp;&nbsp;<span className={s.btnColor} onClick={data.action}> {data.text} </span> </p>
            <div className={s.container}>
                <div  className={`${data.style} ${'code'}`}>
                    <code>
                        {valueState}
                    </code>
                </div>
                {data.action.name === 'dropUp' &&
                    <p className={s.btnColor} onClick={data.action}>{data.text}</p>
                }
            </div>
        </>
    )
}

const StoreState = (props) => {
    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }
    return (
        <>
            {source && 
                <p>Посмотреть содержание store.js &nbsp;&nbsp;<span className={s.btnColor} onClick={drop}>&gt;&gt;</span> </p>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62), item: props.item})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть', item: props.item})}
                </Fragment>
            }
        </>
    )

}

export default StoreState;