import React, { Fragment, useState } from 'react';
import s from './StoryIndex.module.css';
import strStory from './strStoryIndex';



let unhide = (data) => {

    return (
        <>
            <p>Посмотреть содержание Index.jsx &nbsp;&nbsp;<span className={s.btnColor} onClick={data.action}> {data.text} </span> </p>
            <div className={s.container}>
                <div  className={`${data.style} ${'code'}`}>
                    <code>
                        {strStory}
                    </code>
                </div>
                {data.action.name === 'dropUp' &&
                    <p className={s.btnColor} onClick={data.action}>{data.text}</p>
                }
            </div>
        </>
    )
}

const StoryIndex = () => {
    let [isDown, setEditDown] = useState(false);
    let [source, setEditSource] = useState(true);

    const dropdown = () => {
        setEditDown(true);
    }

    const dropUp = () => {
        setEditDown(false);
    }
    const drop = () => {
        setEditSource(false);
        setEditDown(true);
    }
    return (
        <>
            {source && 
                <p>Посмотреть содержание Index.jsx &nbsp;&nbsp;<span className={s.btnColor} onClick={drop}>&gt;&gt;</span> </p>
            }
            {!isDown && !source &&
                <Fragment>
                    {unhide({action: dropdown, style: `${s.downOut}`, text: String.fromCodePoint(62, 62)})}
                </Fragment>
            }
            {isDown && !source &&
                <Fragment>
                    {unhide({action: dropUp, style: `${s.growDown}`, text: 'Скрыть'})}
                </Fragment>
            }
        </>
    )

}

export default StoryIndex;