import React from 'react';
import s from './StepThird.module.css';
import strHtml from './strHtml';
import ContentIndex from './ContentIndex';
import ContentStyle from './ContentStyle';
import PrototypeSite from '../PrototypeSite/PrototypeSite3/PrototypeSite3';




const StepThird = () => {
    return (
        <>
            <p>Для этого, в нужной папке, создаю два файла: index.html и  style.css.</p>
            <p>Если это делать в редакторах VSCode или Sublime Text, надо открыть редактор, создать новый файл, назвать index.html, откроется редактор файла в нем вводим восклицательный знак !  и жмем клавишу Tab или Enter.</p>
            <p>Появится шаблон структуры документа html</p>
            <div className={s.conteiner}>
                <div className="code">
                    <code>
                        {strHtml}
                    </code>
                </div>
            </div>
            <p> Далее наполняем index.html и style.css, чтобы получилась наша верстка.</p>
            <ContentIndex/>
            <ContentStyle/>
            <PrototypeSite/>
        </>
    )
}

export default StepThird;