import React from "react";
import s from "./FileIndexJs.module.css"

const FileIndexJs = () => {

    const text =
`import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);` 


    return (
        <div className={s.container}>
            <p>В файл App.js:</p>
            <div className="code">
                <code>
                    {text}
                </code>
            </div>
        </div>
    )
}

export default FileIndexJs;