let strStoreApp = 
`import './App.css';
import React from 'react';
import Header from './components/Header/Header';
import Options from './components/Options/Options';
import Control from './components/Control/Control';
import store from './redus/state';

function App(props) {
  return (
    <div className="appWrapper">
      <Header state={props.store.state}/>
      <Options state={props.store.state} versionAnswer = {props.store.versionAnswer.bind(store)}/>
      <Control addAnswer = {props.store.addAnswer.bind(store)}/>
    </div>
  )
}

export default App;
`
export default strStoreApp;
